h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.3;
  font-weight: 700;
  font-family: var(--font-headings);
}
@media (min-width: 40em) {
  h1,
h2,
h3 {
    font-family: var(--font-headings);
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--very-dk-blue);
  font-family: var(--font-headings);
}